.swiper {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.swiper-slide > a > img {
    @media (max-width: 766px) {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.swiper {
    width: 100%;
    height: 480px;
    margin-left: auto;
    margin-right: auto;
    cursor: grab;

    @media (max-width: 766px) {
        height: 320px;
    }

    @media (max-width: 520px) {
        height: 200px;
    }
}

.swiper-slide {
    position: relative;
    background-size: cover;
    background-position: center;
    & > .swiper-slide__description {
        position: absolute;
        opacity: 1;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 40%;
        background-color: rgba($color: #000000, $alpha: 0.3);
        color: #fff;
        text-align: left;
        padding-left: 14px;
        transition: all 0.3s ease-in-out;

        & h3 {
            @include font_p1($font-family, $clr_text, 700);
        }
        & p {
            @include font_small($font-family, $clr_text, 700);
            margin: 5px;
        }

    }
    &:hover {
        & > .swiper-slide__description {
            opacity: 0;
            width: 0;
        }
    }
}

.mySwiper2 {
    width: 100%;
}

.mySwiper {
    height: 200px;
    box-sizing: border-box;
    padding: 10px 0;
}

@media (max-width: 620px) {
    .mySwiper {
        height: 100px;
    }
}

.mySwiper .swiper-slide {
    width: 25%;
    height: 100%;
    opacity: 0.4;
}

.mySwiper .swiper-slide-thumb-active {
    opacity: 1;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.fancybox__container {
    z-index: 10000001 !important;
}
